import * as THREE from 'three';

export default function _crayfishdiffuse() {
  return new THREE.MeshStandardMaterial({
    color: new THREE.Color(0.8863, 0.8863, 0.8863),
    roughness: 0.97,
    metalness: 0,
    opacity: 1,
    transparent: false,
    emissive: new THREE.Color(0, 0, 0),
    emissiveIntensity: 1
  });
}